:root {
  --primary: #047857;
  --bg-light: #f0fdf4;
}
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: white;
  margin: 0;
  padding: 0;
}
.header-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--primary);
}
.hero-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.hero-subtitle {
  font-size: 1.25rem;
  color: #4b5563;
  margin-bottom: 1.5rem;
}
.button-primary {
  background-color: var(--primary);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 9999px;
  transition: background-color 0.3s ease;
}
.button-primary:hover {
  background-color: #065f46;
}
.product-card {
  background-color: white;
  border-radius: 1rem;
  padding: 1rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  text-align: center;
}
.footer {
  background-color: #1f2937;
  color: white;
  padding: 1.5rem 0;
  text-align: center;
}
