/* Base styles */
body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
}
.heroImage {
  z-index: 1;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #eee;
  padding: 10px 20px;
  font-size: 14px;
}

.logo {
  font-weight: bold;
  color: #e60012;
}

.menu a {
  margin: 0 10px;
  text-decoration: none;
  color: #333;
}

.actions {
  display: flex;
  align-items: center;
}

.actions input {
  padding: 5px;
  margin-right: 10px;
}

.actions button {
  background: #666;
  color: white;
  padding: 5px 10px;
  border: none;
  cursor: pointer;
}

.hero {
  background-size: cover;
  background-position: center;
  height: 600px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 50px;
}

.overlay {
  background: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 20px;
  max-width: 400px;
  text-align: left;
  z-index:2;
}

.cta {
  background: white;
  color: black;
  padding: 10px 20px;
  border: none;
  margin-top: 10px;
  cursor: pointer;
}

.section {
  text-align: center;
  padding: 50px 20px;
}

.section h2 {
  color: #e60012;
  font-size: 24px;
}

.section p {
  font-size: 16px;
  color: #333;
}
